p,
ul,
h2 {
  margin: 0;
}

ul {
  padding: 0;
}

.list {
  list-style: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  width: 500px;
  padding: 15px 25px 15px 25px;
  background-color: #e7f3f3;
}

.friend-list {
  text-align: center;
  color: #3a1b14;
  font-size: 24px;
  margin-top: 30px;
  margin-bottom: 20px;
}

.listItem {
  display: flex;
  list-style: none;
  width: 50%;
  margin: 10px auto;
  padding: 10px;
  background-color: white;
  border-radius: 4px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

.status {
  display: block;
  width: 12px;
  height: 12px;
  margin-top: 18px;
  margin-right: 15px;
  border-radius: 50%;
  background-color: #c6ec39;
}

